import cmdAvatar from '@/components/cmd-avatar/cmd-avatar.vue';
import cmdIcon from '@/components/cmd-icon/cmd-icon.vue';
import cmdCellItem from '@/components/cmd-cell-item/cmd-cell-item.vue';
import qr from '@/components/tki-qrcode/tki-qrcode.vue';
import { ewm } from '@/api/XFs.js';
export default {
	components: {
		cmdAvatar,
		cmdCellItem,
		cmdIcon,
		qr
	},
	data() {
		return {
			ewmHidden: true,
			val: 'fdsfsdfsdfasf', // 要生成的二维码值
			size: 200, // 二维码大小
			unit: 'upx', // 单位
			background: '#b4e9e2', // 背景色
			foreground: '#309286', // 前景色
			pdground: '#32dbc6', // 角标色
			icon: '', // 二维码图标
			iconsize: 40, // 二维码图标大小
			lv: 3, // 二维码容错级别 ， 一般不用设置，默认就行
			onval: true, // val值变化时自动重新生成二维码
			loadMake: false, // 组件加载完成后自动生成二维码
			src: '', // 二维码生成后的图片地址或base64
			elements: [
				
				{
					title: '积分兑换',
					name: ' Record ',
					color: 'olive',
					cuIcon: 'moneybagfill',
					url: '/pages/YongHu/changehome',
					type: '商户'
				},
				{
					title: '兑换记录',
					name: 'Information',
					color: 'green',
					cuIcon: 'moneybagfill',
					url: '/pages/business/addRecord',
					type: '商户'
				},
			],
			swiperList: [
				{
					id: 0,
					type: 'image',
					url: 'https://ljfl.hztlcgj.com/image/head4.png'
				},
				{
					id: 1,
					type: 'image',
					url: 'https://ljfl.hztlcgj.com/image/head4.png'
				}
			],
			dotStyle: false,
			usertype: ''
		};
	},
	mounted() {
		this.TowerSwiper('swiperList');
		// 初始化towerSwiper 传已有的数组名即可
	},
	created() {
		this.usertype = this.$store.state.roleName;
		let count = this.$store.state.EWMShowCount;
		if (count) {
			this.showMyRqcod();
		}
	},
	methods: {
		// 初始化towerSwiper
		TowerSwiper(name) {
			let list = this[name];
			for (let i = 0; i < list.length; i++) {
				list[i].zIndex = parseInt(list.length / 2) + 1 - Math.abs(i - parseInt(list.length / 2));
				list[i].mLeft = i - parseInt(list.length / 2);
			}
			this.swiperList = list;
		},
		// v-show="Test(item.type)"
		// Test(v) {
		// 	if (this.usertype.indexOf(v) != -1) return true;
		// },
		around() {
			uni.redirectTo({
				url: '/pages/YongHu/around'
			});
		},
		typelist() {
			uni.redirectTo({
				url: '/pages/garbage/typelist'
			});
		},
		scoreget() {
			uni.redirectTo({
				url: '/pages/YongHu/ScoreSearch'
			});
		},
		ZYZ() {
			uni.redirectTo({
				url: '/pages/YongHu/ZYZ'
			});
		},
		myQInfo() {
			uni.redirectTo({
				url: '/pages/user/info/info'
			});
		},
		//普通用户已进入就显示二维码
		showMyRqcod() {
			this.$store.state.EWMShowCount = false;
			if (this.usertype === '普通用户') {
				this.ewmHidden = false;
				ewm(this.$store.state.userId).then(res => {
					console.log(res);
					if (res.data.code == 200) this.val = res.data.data;
				});
			}
		},
		//点击遮罩层隐藏
		hide() {
			this.ewmHidden = true;
		}
	}
};
